.header {
    background: #FFFCFC;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.15);
}

.quantity {
    display: flex;
    align-items: center;
    justify-content: start;
    padding: 0;
}
.quantity__minus,
.quantity__plus {
    display: block;
    width: 22px;
    height: 23px;
    margin: 0;
    background: #dee0ee;
    text-decoration: none;
    text-align: center;
    line-height: 23px;
}
.quantity__minus:hover,
.quantity__plus:hover {
    background: #575b71;
    color: #fff;
}
.quantity__minus {
    border-radius: 3px 0 0 3px;
}
.quantity__plus {
    border-radius: 0 3px 3px 0;
}
.quantity__input {
    width: 32px;
    height: 23px;
    margin: 0;
    padding: 0;
    text-align: center;
    border-top: 2px solid #dee0ee;
    border-bottom: 2px solid #dee0ee;
    border-left: 1px solid #dee0ee;
    border-right: 2px solid #dee0ee;
    background: #fff;
    color: #8184a1;
    outline: none;
}
.quantity__minus:link,
.quantity__plus:link {
    color: #8184a1;
}
.quantity__minus:visited,
.quantity__plus:visited {
    color: #fff;
}


.overlay {
    background: rgba(196, 196, 196, 0.29);
    backdrop-filter: blur(4px);
}

.header {
    background: #FFFCFC;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.15);
}

